<template>
  <div class="cart-checkout-container">
    <div class="cart-checkout">
      <h4>Choose payment method</h4>
      <div class="cart-checkout--element"
           :class="[ getPaymentData === 'card' ? 'selected' : 'no-selected' ]"
           v-on:click="selectPayment('card')">
        <span>Karta kredytowa</span>
        <img src="../../assets/cart/mastercard.png" alt="">
        <img src="../../assets/cart/visa.png" alt="">
      </div>
      <div class="cart-checkout--element"
           :class="[ getPaymentData === 'paypal' ? 'selected' : 'no-selected' ]"
           v-on:click="selectPayment('paypal')">
        <span>PayPal</span>
        <img src="../../assets/cart/paypal.png" alt="">
      </div>
      <div class="cart-checkout--element"
           :class="[ getPaymentData === 'local' ? 'selected' : 'no-selected' ]"
           v-on:click="selectPayment('local')">
        <span>Lokalne metody płatności w Polsce</span>
        <img src="../../assets/cart/24b.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'CheckoutDetails',
  data() {
    return {
      selectedPayment: '',
    };
  },
  methods: {
    ...mapActions({
      addPaymentMethod: 'cart/addPaymentMethod',
    }),
    selectPayment(payment) {
      this.addPaymentMethod(payment);
    },
  },
  computed: {
    ...mapGetters({
      getPaymentData: 'cart/getPaymentData',
    }),
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .cart-checkout-container {
    margin-top: $inner-block-margin;
  }
  h4 {
    margin-bottom: $padding;
  }
  .cart-checkout {
    background-color: white;
    border-radius: 5px;
    padding: $inner-block-margin $padding;
    width: 100%;
    min-width: 430px;
    &--element {
      margin-bottom: $padding;
      display: flex;
      justify-content: center;
      & span {
        margin-right: $basic-padding;
      }
      &:hover {
        @include grey-gradient();
        @include button-shadow();
        padding: $small-padding $padding;
        border-radius: 5px;
      }
      &.selected {
        padding: $small-padding $padding;
        border-radius: 5px;
        @include primary-gradient();
        color: white;
      }
    }
  }

</style>



