<template>
  <div class="navbar-wrapper">
    <vs-navbar v-model="activeItem">
      <div slot="title">
        <vs-navbar-item index="5">
          <router-link to="/" class="logo"><img src="../assets/logo.png" alt=""></router-link>
        </vs-navbar-item>
      </div>
      <vs-navbar-item index="0">
        <router-link to="/shop">SHOP</router-link>
      </vs-navbar-item>
      <vs-navbar-item index="1">
        <router-link to="/aboutus">ABOUT US</router-link>
      </vs-navbar-item>
      <vs-navbar-item index="2">
        <router-link to="/sizecare">SIZE & CARE</router-link>
      </vs-navbar-item>
      <vs-navbar-item index="3">
        <router-link to="/contact">CONTACT</router-link>
      </vs-navbar-item>
      <vs-navbar-item index="4">
        <router-link to="/cart">
          <div class="shopping-cart">
            <font-awesome-icon icon="shopping-bag"/>
            <font-awesome-icon class="shopping-cart--label" icon="circle"/>
            <label class="shopping-cart--counter">{{itemsCounter}}</label>
          </div>
        </router-link>
      </vs-navbar-item>
    </vs-navbar>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data: () => ({
    name: 'Navigation',
    activeItem: null,
  }),
  computed: {
    ...mapState({
      itemsCounter: state => state.cart.itemsCounter,
    }),
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .navbar-wrapper {
    background-color: white;
  }
  .vs-navbar{
    max-height: 80px;
    &--item {
      margin-right: 32px;
      a {
        font-size: 14px;
        font-weight: bold;
        padding: 0;
      }
      a.logo {
        padding: 6px 0px 6px 32px;
      }
      .fa-shopping-bag {
        color: white;
      }
    }
    .is-active-item {
      color: black;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: -10px;
        width: 100%;
        height: 1px;
        background-color: $primary-color;
        box-shadow: $card-shadow;
      }
    }
    .shopping-cart {
      padding: 13px 15px;
      background-color: $primary-color;
      border-radius: 6px;
      box-shadow: $card-shadow;
      &--counter{
        font-weight: bold;
        color: black;
        position: absolute;
        font-size: 7px;
        top: 11px;
        right: 13px;
      }
      &--label {
        position: absolute;
        font-size: 12px;
        color: white;
        top: 9px;
        right: 9px;
        path {
          box-shadow: 0px 1px 1px 0px rgba(0,0,0,1);
        }
      }
    }
  }
</style>



