<template>
  <div class="cart">
    <vs-row
      vs-align="flex-start"
      vs-type="flex"
      vs-justify="center"
      vs-w="12"
      v-if="checkoutStep > 0 && checkoutStep < 4"
    >
      <CheckoutStep></CheckoutStep>
      <vs-col
        vs-w="5"
        vs-type="flex"
        vs-justify="center"
        vs-align="center"
        class="outer-card"
      >
        <ProductDetails v-if="checkoutStep === 1"></ProductDetails>
        <DeliveryDetails v-if="checkoutStep === 2"></DeliveryDetails>
        <CheckoutDetails v-if="checkoutStep === 3"></CheckoutDetails>
      </vs-col>
      <vs-col
        vs-w="3"
        vs-type="flex"
        vs-justify="center"
        vs-align="flex-start"
        class="summary-card"
      >
        <div class="order-summary card-shadow">
          <h4>Order Summary</h4>
          <div class="order-summary--left">
            <p>1 PRODUCT</p>
            <p>DELIVERY</p>
            <h4>TOTAL:</h4>
          </div>
          <div class="order-summary--right">
            <p
              v-for="(product, index) in cartProducts"
              :key="index"
            ><b>{{product.price}}</b></p>
            <p><b class="free">FREE</b></p>
            <h4 class="total"><b>{{cartTotalPrice}} PLN</b></h4>
          </div>
          <vs-button
            :disabled="proceedButtonState"
            color="danger"
            class="primary-button primary-button-checkout"
            type="filled"
            @click="changeCheckoutStep"
          >PROCEED TO CHECKOUT</vs-button>
        </div>
      </vs-col>
    </vs-row>
    <vs-row
      v-if="checkoutStep === 4"
      vs-w="12" vs-type="flex"
      vs-justify="center"
      vs-align="flex-start"
    >
      <vs-col vs-w="8">
        <div class="thank-page card-shadow">
          <h2>Zamówienie pozostało zrealizowano!</h2>
          <p>Lecz nie śpieszcie odchodzić, mamy coś dla Ciebie! Jeżeli czytasz to,
            oznacza że pozostałeś jednym z posiadaczy limitowanej koszulki od Brevis
            Tale. Do twojego zamówienia dołączyliśmy unikalną bransoletkę. Ponieważ
            nie przeszłęś przez ideę Bravis Tale oddajemy ją Ci. Bransoletkę nie można
            kupić, tylko otrzymać. Teraz ona twoja! <b>#BrevisTale #DuringTheTalk</b>.</p>
        </div>
      </vs-col>
    </vs-row>
    <vs-row
      v-if="checkoutStep === 0"
      vs-w="12" vs-type="flex"
      vs-justify="center"
      vs-align="flex-start"
      class="empty-cart"
    >
      <vs-col>
        <h4>Cart Is Empty. Please return to shop</h4>
        <vs-button color="danger" type="filled"
                   class="primary-button"
        to="/shop">
          TO SHOP
        </vs-button>
      </vs-col>
    </vs-row>
    <Footer></Footer>
  </div>
</template>

<script>
import ProductDetails from '@/components/cart/ProductDetails';
import DeliveryDetails from '@/components/cart/DeliveryDetails';
import CheckoutDetails from '@/components/cart/CheckoutDetails';
import CheckoutStep from '@/components/cart/CheckoutSteps';
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
  name: 'Cart',
  components: {
    ProductDetails,
    DeliveryDetails,
    CheckoutDetails,
    CheckoutStep,
  },
  data() {
    return {
    };
  },
  computed: {
    ...mapState({
      product: state => state.cart.product,
      itemsCounter: state => state.cart.itemsCounter,
    }),
    ...mapGetters({
      cartTotalPrice: 'cart/cartTotalPrice',
      cartProducts: 'cart/cartProducts',
      checkoutStep: 'cart/getCheckoutStep',
      getDeliveryData: 'cart/getDeliveryData',
      getPaymentData: 'cart/getPaymentData',
    }),
    proceedButtonState() {
      if (this.checkoutStep === 1) {
        return false;
      }
      if (this.checkoutStep === 2) {
        if (this.getDeliveryData.method && this.getDeliveryData.addressData) {
          return false;
        }
      }
      if (this.checkoutStep === 3) {
        if (this.getPaymentData) {
          return false;
        }
      }
      return true;
    },
  },
  methods: {
    ...mapActions({
      changeCheckoutStep: 'cart/changeCheckoutStep',
    }),
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .cart {
    h4 {
      font-size: 21px;
      font-weight: 900;
      color: black;
    }
  }
  .outer-card {
    max-width: 500px;
    width: 100%;
    margin-right: $block-margin;
  }
  .summary-card {
  }
  .order-summary {
    margin-top: $inner-block-margin;
    background-color: white;
    padding: $inner-block-margin;
    border-radius: 5px;
    h4 {
      margin-bottom: $padding;
    }
    p {
      font-size: 13px;
      font-weight: 400;
      color: black;
      margin-bottom: $basic-padding;
    }
    b {
      font-weight: 900;
    }
    &--left {
      text-align: left;
      display: inline-block;
      width: 49%;
    }
    &--right {
      text-align: right;
      display: inline-block;
      width: 49%;
    }
    .free {
      color: $primary-color;
    }
    .total {
      margin-top: $basic-padding;
    }
    .primary-button-checkout {
      margin-top: $padding;
      font-weight: bold;
      font-size: 19px;
      padding: $basic-padding;
    }
  }
  .thank-page {
    height: 40vh;
    margin-top: $block-margin;
    padding: $inner-block-margin;
    background-color: white;
    border-radius: 5px;

    h2 {
      font-size: 42px;
      font-weight: 900;
      color: $primary-color;
      margin-bottom: $padding;
    }
    b {
      color: $primary-color;
      font-weight: 900;
    }
  }
  .empty-cart {
    margin-top: $block-margin;
    height: 40vh;
    h4 {
      margin-bottom: $padding;
    }
  }
</style>



