<template>
  <section id="about">
    <div class="about-cards">
      <vs-row>
        <vs-col vs-w="12">
          <CurtainCard
            v-for="(card, index) in cardsCurtains"
            :key="index"
            v-bind:content="card"
          ></CurtainCard>
        </vs-col>
      </vs-row>
    </div>
    <div class="legend">
      <vs-row>
        <vs-col vs-w="12"></vs-col>
      </vs-row>
      <h2>LEGEND</h2>
      <div class="legend-card card-shadow">
        <div class="legend-card--text">
          <p>{{legendText}}}</p>
        </div>
      </div>
    </div>
    <div class="advantages-cards">
      <vs-row>
        <AdvantagesCard
          v-for="(card, index) in cardsAdvantages"
          :key="index"
          v-bind:content="card"
          v-bind:smaller="false">
        </AdvantagesCard>
      </vs-row>
    </div>
    <div class="slogan-mission">
      <SloganLine v-bind:content="sloganContent"></SloganLine>
    </div>
    <Footer></Footer>
  </section>
</template>

<script>
import CurtainCard from '@/components/aboutus/CurtainCard';
import AdvantagesCard from '@/components/AvantagesCard';
import SloganLine from '@/components/SloganLine';

export default {
  name: 'AboutUs',
  components: {
    CurtainCard,
    AdvantagesCard,
    SloganLine,
  },
  data() {
    return {
      sloganContent: {
        header: 'MISSION',
        text: 'Wybicie się z rutyny - życie przepełnione szarością, chcemy napełnić jaskrawymi barwami.',
      },
      cardsCurtains: [{
        id: 0,
        text: 'Na rynku odzieżowym spotkaliśmy się z dużą liczbą identycznych za swoim wyglądem towarów. Różnokolorowe zdjęcia, duże rozmiary, hasła standardowo w centrum - typowy sposób realizacji. Wszystko to przyciąga uwagę, rzuca się w oczy. Chcieliśmy innego - wyróżniać się niepozornie.',
        lighterUrl: '/static/about/lighter_problem.png',
      }, {
        id: 1,
        text: 'Na rynku odzieżowym spotkaliśmy się z dużą liczbą identycznych za swoim wyglądem towarów. Różnokolorowe zdjęcia, duże rozmiary, hasła standardowo w centrum - typowy sposób realizacji. Wszystko to przyciąga uwagę, rzuca się w oczy. Chcieliśmy innego - wyróżniać się niepozornie.',
        lighterUrl: '/static/about/lighter_idea.png',
      }, {
        id: 2,
        text: 'Na rynku odzieżowym spotkaliśmy się z dużą liczbą identycznych za swoim wyglądem towarów. Różnokolorowe zdjęcia, duże rozmiary, hasła standardowo w centrum - typowy sposób realizacji. Wszystko to przyciąga uwagę, rzuca się w oczy. Chcieliśmy innego - wyróżniać się niepozornie.',
        lighterUrl: '/static/about/lighter_solution.png',
      }],
      legendText: 'Wędrowiec natrafił na (małą) wioskę znajdującą się u podnóża góry. W miejscowej\n' +
        '            tawernie poznał mieszkańca tej wioski, mężczyznę w podeszłym wieku o imieniu Brevis.\n' +
        '            Był on miejscowym bajarzem i podczas pobytu w tawernie opowiedział wędrowcowi kilka\n' +
        '            ciekawych historii o jego kraju. Brevis był ubrany jak wszyscy, za wyjątkiem jego górnej\n' +
        '            odzieży. Z wyglądu ona była bardzo stara, a od spodu widniały wyhaftowane obrazy jelenia\n' +
        '            i wilka. Zobaczywszy zainteresowanie swoim ubiorem opowiedział historię ze swojego życia.\n' +
        '            W młodości, zbierając chrust, został zaatakowany przez wilka. W nierównej walce\n' +
        '            stracił on wszystkie siły i czekał na śmierć, aż tu znikąd pojawił się jeleń. Wystawiwszy\n' +
        '            poroże nastraszył wilka i zmusił go do rychłej ucieczki. Na znak pamięci tego dnia,\n' +
        '            Brevis pozostawił sobie zniszczoną odzież, w której był w tamtym dniu. Zaszywszy podarte\n' +
        '            miejsca obrazami dwóch zwierząt, zawsze jest chętny do opowiedzenia wszystkim\n' +
        '            zainteresowanym historię o niespodziewanym ratunku.',
      cardsAdvantages: [{
        id: 0,
        img_url: 'static/about/loop.svg',
        header: 'GOD IN DETAILS',
        text: 'Głównym powodem powstania Brevis Tale \n' +
          'posłużył problem. W dzisiejszych czasach \n' +
          'jedni wyróżniają się wybierając znane \n' +
          'marki, drudzy podążają za modą,',
      }, {
        id: 1,
        img_url: 'static/about/vesy.svg',
        header: 'GOD IN DETAILS',
        text: 'Głównym powodem powstania Brevis Tale \n' +
          'posłużył problem. W dzisiejszych czasach \n' +
          'jedni wyróżniają się wybierając znane \n' +
          'marki, drudzy podążają za modą,',
      }, {
        id: 2,
        img_url: 'static/about/paintset.svg',
        header: 'GOD IN DETAILS',
        text: 'Głównym powodem powstania Brevis Tale \n' +
          'posłużył problem. W dzisiejszych czasach \n' +
          'jedni wyróżniają się wybierając znane \n' +
          'marki, drudzy podążają za modą,',
      }],
    };
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .about-cards {
    margin-top: $block-margin;
  }
  .legend {
    h2 {
      font-size: 84px;
      margin-bottom: $basic-padding;
      color: $primary-color;
    }
    margin-top: $block-margin;
   &-card {
     background-color: white;
     height: 120%;
     padding: 50px $block-margin/2;
     margin: 0 50px;
     &--text {
       p {
         position: relative;
         display: block;
         margin-top: 50px;
         margin-bottom: 50px;
         &:before {
           content: '';
           background-image: url("../assets/about/left-quote.svg");
           position: absolute;
           left: -50px;
           top: -35px;
           height: 46px;
           width: 46px;
         }
         &:after {
           content: '';
           background-image: url("../assets/about/right-quote.svg");
           position: absolute;
           right: -50px;
           bottom: -35px;
           height: 46px;
           width: 46px;
         }
       }
       padding: $block-margin/2 $block-margin*1.5;
     }
    }
  }
  .advantages {
    margin-top: $block-margin;

    &-cards {
      width: 80%;
      margin: auto;
    }
    &-card {
      img {
        height: 100px;
        width: auto;
      }
    }
  }
  .slogan-mission {
    margin-top: $block-margin;
    background-color: white;
  }
</style>



