const cart = {
  namespaced: true,
  state: {
    product: {
      id: 0,
      title: 'Brevis Tale Legend Men`s T- Shirt',
      price: '129,00',
      stock: '49',
      color: 'Scarlet',
      size: { name: 'XS' },
      description: 'Classic single jersey T-Shirt with elastane in small rib\n' +
        '            neckline, produced of long staple combed cotton, the particularly\n' +
        '            fine surface provides a soft hand feel and is highly suitable for\n' +
        '            any kind of textile finishing. This Premium product is highly durable\n' +
        '            and has a stable shape. Shoulder seams with stay tape and double seams\n' +
        '            as well as back neck tape complete this product. The T-Shirt has a\n' +
        '            modern cut and is made from tubular fabric.',
    },
    products: [
      {
        id: 0,
        title: 'Brevis Tale Legend Men`s T- Shirt',
        price: '129,00',
        stock: '49',
        color: 'Scarlet',
        size: { name: 'XS' },
        description: 'Classic single jersey T-Shirt with elastane in small rib\n' +
          '            neckline, produced of long staple combed cotton, the particularly\n' +
          '            fine surface provides a soft hand feel and is highly suitable for\n' +
          '            any kind of textile finishing. This Premium product is highly durable\n' +
          '            and has a stable shape. Shoulder seams with stay tape and double seams\n' +
          '            as well as back neck tape complete this product. The T-Shirt has a\n' +
          '            modern cut and is made from tubular fabric.',
      }
    ],
    // shape items: [{ id, quantity }]
    items: [],
    checkoutStatus: null,
    checkoutStep: 0,
    itemsCounter: 0,
    // shape delivery: {method: {name}, addressData: {data}}
    delivery: {
      method: null,
      addressData: null,
    },
    payment: {
      name: null,
    },
  },
  mutations: {
    pushProductToCart: (state, { id }) => {
      state.items.push({
        id,
        quantity: 1,
      });
      state.itemsCounter++;
    },
    incrementItemQuantity: (state, { id }) => {
      const cartItem = state.items.find(item => item.id === id);
      cartItem.quantity++;
      state.itemsCounter++;
    },
    setItemQuantity: (state, { id, counter }) => {
      const cartItem = state.items.find(item => item.id === id);
      cartItem.quantity = counter;
      state.itemsCounter += cartItem.quantity;
    },
    incrementCheckoutStep: (state) => {
      state.checkoutStep +=1;
      if (state.checkoutStep > 4) {
        state.checkoutStep = 1;
      }
    },
    setCartItems: (state, { items }) => {
      state.items = items;
    },
    setCheckoutStatus: (state, status) => {
      state.checkoutStatus = status;
    },
    setCheckoutStep: (state, step) => {
      state.checkoutStep = step;
    },
    setDeliveryAddress: (state, item) => {
      state.delivery.addressData = item;
    },
    setDeliveryMethod: (state, method) => {
      state.delivery.method = method;
    },
    setPaymentMethod: (state, payment) => {
      state.payment.name = payment;
    },
  },
  actions: {
    checkout: ({ commit, state }, product) => {
      const savedCartItems = [...state.items];
      commit('setCheckoutStatus', null);
    },
    changeItemsCount: ({ commit, state }, id, value) => {
      commit('setItemQuantity', id, value);
    },
    changeCheckoutStep: ({ commit, state }) => {
      if (state.checkoutStep !== 4) {
        commit('incrementCheckoutStep');
      } else {
        commit('setCheckoutStep', 1);
      }
    },
    addDeliveryAddress({ state, commit }, address) {
      if (Object.keys(address).length !== 0) {
        console.log(address);
        commit('setDeliveryAddress', address);
      } else {
        console.log('received empty address');
      }
    },
    addDeliveryMethod({ state, commit }, method) {
      if (Object.keys(method).length !== 0) {
        console.log(method);
        commit('setDeliveryMethod', method);
      } else {
        console.log('received empty method');
      }
    },
    addPaymentMethod({ state, commit }, method) {
      if (Object.keys(method).length !== 0) {
        console.log(method);
        commit('setPaymentMethod', method);
      } else {
        console.log('received empty method');
      }
    },
    addProductToCart ({ state, commit }, product) {
      commit('setCheckoutStatus', null);
      if (product.stock > 0) {
        const cartItem = state.items.find(item => item.id === product.id);
        if (!cartItem) {
          commit('pushProductToCart', { id: product.id });
          commit('incrementCheckoutStep');
        } else {
          commit('incrementItemQuantity', cartItem);
        }
      }
    },
  },
  getters: {
    getProduct: (state) => {
      return state.product;
    },
    cartProducts: (state) => {
      const mapArr = state.items.map(({ id, quantity }) => {
        const product = state.products.find(product => product.id === id);
        return {
          title: product.title,
          price: product.price,
          quantity,
        }
      });
      console.log(mapArr);
      return mapArr;
    },
    cartTotalPrice: (state, getters) => {
      const total = getters.cartProducts.reduce((total, product) => {
        const parsingPrice = parseFloat(product.price);
        return total + parsingPrice * product.quantity
      }, 0);
      console.log(total);
      return total;
    },
    getDeliveryAddress(state) {
      if(state.delivery.addressData !== null) {
        return state.delivery.addressData;
      }
      return null;
    },
    getDeliveryMethod: (state) => {
      return state.delivery.method.name;
    },
    getDeliveryData: (state) => {
      return state.delivery;
    },
    getPaymentData: (state) => {
      return state.payment.name;
    },
    getCheckoutStep: (state) => {
      return state.checkoutStep;
    },
    getCartItemQuantity: (state, id) => {
      const item = state.items.find(item => item.id === id);
      return item.quantity;
    },
  },
};

export default cart;



