<template>
    <vs-row vs-w="12"
            vs-align="flex-start"
            vs-type="flex"
            vs-justify="center">
      <vs-col vs-w="8">
        <div class="checkout-step">
          <div class="checkout-step--item">
            <div class="first-step">
              <div class="circle"
                   :class="[ getCheckoutStep > 0 ? 'circle-checked' : '' ]">1</div>
              <span>Product details</span>
            </div>
          </div>
          <div class="checkout-step--item">
            <div class="second-step">
              <div class="circle"
                   :class="[ getCheckoutStep > 1 ? 'circle-checked' : '' ]">2</div>
              <span>Shipping</span>
            </div>
          </div>
          <div class="checkout-step--item">
            <div class="third-step">
              <div class="circle"
                   :class="[ getCheckoutStep > 2 ? 'circle-checked' : '' ]">3</div>
              <span>Payment</span>
            </div>
          </div>
        </div>
      </vs-col>
    </vs-row>
</template>

<script>
import { mapState, mapGetters } from 'vuex';

export default {
  name: 'checkoutSteps',
  data() {
    return {
    };
  },
  computed: {
    ...mapState({}),
    ...mapGetters({
      getCheckoutStep: 'cart/getCheckoutStep',
    }),
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .checkout-step {
    display: flex;
    width: 100%;
    font-size: 13px;
    color: black;
    font-weight: 900;
    justify-content: center;
    margin-top: $inner-block-margin;
    &--item {
      width: 10%;
      text-align: center;
      .circle {
        position: relative;
        display: inline-block;
        text-align: center;
        height: $inner-block-margin;
        width: $inner-block-margin;
        @include grey-gradient;
        border-radius: 50%;
        padding-top: 18px;
        color: black;
        @include checkout-step-shadow;
        &:after {
          content: '';
          position: absolute;
          width: $padding;
          height: 1px;
          top: 50%;
          left: 160%;
          background: $bottom-border-color;
        }
        &-checked {
          color: white;
          @include primary-gradient;
          &:after {
            @include button-shadow;
            background: $primary-color;
          }
        }
      }
      span {
        margin-top: $basic-padding;
        display: block;
      }
      &:last-child{
        margin-right: 0;
        .circle {
          &:after {
            width: 0;
          }
        }
      }
      margin-right: $inner-block-margin;
    }
  }
</style>



