<template>
  <div class="cart-details">
    <h4 class="cart-details--title">Product details</h4>
    <div class="cart-details--image left-side">
      <img src="static/shop/product_main.png" alt="">
    </div>
    <div class="cart-details--descr right-side">
      <p><b>{{product.title}}</b></p>
      <div class="divider"></div>
      <p>Colour: <b>{{product.color}}</b></p>
      <div class="divider"></div>
      <p>Size: <b>{{product.size.name}}</b></p>
      <div class="divider"></div>
      <p><b>{{product.price}} PLN</b></p>
      <vs-input-number
        v-model="counter"
        class="cart-details--counter"/>
    </div>
    <div class="divider"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'ProductDetails',
  data() {
    return {
      counter: 1,
    };
  },
  watch: {
    counter: () => {
      this.setItemQuantity(0, this.counter);
    },
  },
  methods: {
  },
  computed: {
    ...mapState({
      itemsCounter: state => state.cart.itemsCounter,
      product: state => state.cart.product,
    }),
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .cart-details {
    margin-top: $inner-block-margin;
    background-color: white;
    width: 100%;
    padding: $inner-block-margin;
    h4 {
      margin-bottom: $padding;
    }
    p {
      font-size: 13px;
      font-weight: 400;
      color: black;
      margin-bottom: $small-padding;
    }
    b {
      font-weight: 900;
    }
    .divider {
      margin: 0 0 $small-padding;
    }
    & .left-side {
      display: inline-block;
      width: 49%;
    }
    & .right-side {
      text-align: left;
      display: inline-block;
      width: 49%;
    }
    &--image {
      img {
        max-width: 154px;
        height: 135px;
      }
    }
    &--counter {
      background: transparent;
      padding: 0;
      margin: 0 0 $padding;
      button {
        background: transparent !important;
      }
      i {
        color: black !important;
      }
      input {
        color: black;
        border-radius: 5px;
        border: 1px solid black !important;
        font-size: 13px;
        font-weight: 900;
        min-height: 38px;
        min-height: 38px;
      }
    }
  }
</style>



