<template>
  <section class="contact" id="contact">
    <div class="slogan-contact">
      <SloganLine v-bind:content="sloganContent"></SloganLine>
    </div>
    <div class="info-container card-shadow">
      <vs-row>
        <vs-col vs-w="12">
          <AdvantagesCard
            v-for="card in cardsInfo"
            :key="card.id"
            v-bind:content="card" class="info-container--cards"
            v-bind:button="true"
            v-bind:smaller="true"
            @clicked="onClickMessageType">
          </AdvantagesCard>
        </vs-col>
      </vs-row>
    </div>
    <div class="contact-form" v-if="selected !== ''">
      <h2>{{selected}}</h2>
      <div class="contact-form-container">
        <vs-row  vs-align="flex-start"
                 vs-type="flex" vs-justify="center" vs-w="12">
          <vs-col s-type="flex" vs-w="4">
            <div class="contact-form-input-group">
              <vs-row vs-w="12">
                <vs-col vs-w="6"><label for="name">Imię</label></vs-col>
                <vs-col vs-w="6"><label for="surname">Nazwisko</label></vs-col>
              </vs-row>
              <vs-row vs-w="12">
                <vs-col vs-w="6">
                  <input type="text" id="name" class="contact-form-input--element">
                </vs-col>
                <vs-col vs-w="6">
                  <input type="text" id="surname" class="contact-form-input--element">
                </vs-col>
              </vs-row>
              <vs-row vs-w="12">
                <vs-col vs-w="12">
                  <label for="email">Email</label>
                  <input type="email" id="email" class="contact-form-input--element">
                </vs-col>
              </vs-row>
              <vs-row vs-w="12">
                <vs-col vs-w="12">
                  <label for="theme">Powód</label>
                  <input
                    v-if="selected==='INFO'"
                    type="text" id="theme--info"
                    class="contact-form-input--element"
                  >
                  <input
                    v-if="selected==='REKLAMACJA'"
                    type="text" id="theme--"
                    class="contact-form-input--element"
                    readonly
                    :value=selected
                  >
                  <input
                    v-if="selected==='CUSTOMER SERVICE'"
                    type="text" id="theme"
                    class="contact-form-input--element"
                    readonly
                    :value=selected
                  >
                </vs-col>
              </vs-row>
              <vs-row vs-w="12">
                <vs-col vs-w="12">
                  <label for="message">Wiadomość</label>
                  <textarea rows=4 id="message" class="contact-form-input--element"></textarea>
                </vs-col>
              </vs-row>
              <vs-row vs-w="12">
                <vs-col vs-w="12">
                  <input type="file" id="upload" class="contact-form-input--element">
                  <a class="contact-form-input--element" id="button--file">
                    Attach files<font-awesome-icon icon="plus"/>
                  </a>
                </vs-col>
              </vs-row>
              <vs-row vs-w="12" id="button-send-container">
                <vs-col vs-w="12" vs-align="flex-start"
                        vs-type="flex" vs-justify="center">
                  <vs-button color="danger" type="filled"
                             class="small-button-brevis primary-brevis-btn"
                             id="button--send">
                    WYSŁAĆ</vs-button>
                </vs-col>
              </vs-row>
            </div>
          </vs-col>
        </vs-row>
      </div>
    </div>
    <Footer></Footer>
  </section>
</template>

<script>
import SloganLine from '@/components/SloganLine';
import AdvantagesCard from '@/components/AvantagesCard';

export default {
  name: 'Contact',
  components: {
    SloganLine, AdvantagesCard,
  },
  data() {
    return {
      selected: '',
      sloganContent: {
        header: 'HELLO, IS THERE SOMEONE?',
        text: '',
      },
      cardsInfo: [{
        id: 0,
        img_url: 'static/contacts/information.svg',
        header: 'INFO',
        text: 'Główny e-mail dla kontaktu z nami',
      }, {
        id: 1,
        img_url: 'static/contacts/complaint.svg',
        header: 'REKLAMACJA',
        text: 'Ta sekcja pozwala na złożenie reklamacji \n' +
          'towarów na stronie Brevis Tale.',
      }, {
        id: 2,
        img_url: 'static/contacts/feedback.svg',
        header: 'CUSTOMER SERVICE',
        text: 'We wszystkich sprawach związanych z obsługą \n' +
          'lub produktem.',
      }],
    };
  },
  methods: {
    onClickMessageType(value) {
      this.selected = value;
    },
  },
};
</script>

<style lang="scss">
@import "~@/styles/style.scss";
  .slogan-contact {
    background: white;
  }
  .advantages-card {
    h2 {
      font-size: 21px;
    }
  }
  .info-container {
    margin-top: $inner-block-margin;
    background-color: white;
    border-radius: 8px;
    position: relative;
    display: inline-block;
    width: 80%;
    .homepage-header--button {
      padding: $basic-padding $inner-block-margin;
      font-weight: bold;
      font-size: 13px;
    }
    &--cards {
      margin: $inner-block-margin 0 $block-margin;
    }
  }

  .contact-form {
    margin-top: $inner-block-margin;
    h2 {
      margin-bottom: $inner-block-margin;
    }
    &-container {

    }
    &-input {

      &-group {
        text-align: left;
        label {
          margin-top: $basic-padding;
          display: inline-block;
          width: 49%;
          font-size: 13px;
          font-weight: 400;
        }
      }
      &--element {
        display: inline-block;
        text-align: center;
        width: 100%;
        height: 38px;
        border-radius: 5px;
        border: 1px black solid;
        background: none;
        margin-top: $small-padding;
        font-weight: 900;
        font-size: 13px;
        &:focus {
          background-color: white;
        }
        &#name {
          width: 95%;
        }
        &#surname {
        }
        &#upload {
          width: 0px;
          height: 0px;
          position: absolute;
          border: none;
        }
        &#message {
          height: 152px;
        }
        &#button--file{
          padding-top: 11px;
          svg {
            margin-left: $basic-padding;
          }
        }
      }
    }
    #button-send {
      &-container {
        text-align: center;
        margin-top: $padding;
      }
    }
  }
</style>



