<template>
  <vs-col vs-w="4" class="advantages">
    <div>
        <div class="advantages-card">
          <img :src="post.img_url"
               :class="[ smaller ? 'smaller' : 'normal' ]">
          <h2>{{post.header}}</h2>
          <div class="advantages-card--text">
            <p class="small-text">{{post.text}}</p>
          </div>
          <vs-button
            v-if="buttonTrue" color="danger" type="filled"
            class="primary-button-small" @click="onClickMessage">
            CONTACT WITH US<font-awesome-icon icon="envelope"/></vs-button>
        </div>
    </div>
  </vs-col>
</template>

<script>
export default {
  name: 'AdvantagesCard',
  props: ['content', 'button', 'smaller'],
  data() {
    return {
      post: {
        id: this.content.id,
        img_url: this.content.img_url,
        header: this.content.header,
        text: this.content.text,
      },
      buttonTrue: this.button,
      smallerFlag: this.smaller,
    };
  },
  methods: {
    onClickMessage() {
      this.$emit('clicked', this.post.header);
    },
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .advantages {
    &-card {
      position: relative;
      width: 260px;
      margin: 0 auto;
      img {
        height: 100px;
        width: auto;
      }
      h2 {
        width: 100%;
        padding-bottom: $basic-padding;
        margin: $basic-padding 0;
        border-bottom: 3px solid $primary-color;
        color: black;
      }
      img.smaller {
        height: 65px;
        width: auto;
      }
      img.normal {
        height: 100px;
        width: auto;
      }
      &--text {
        p {
          color: black;
        }
      }
      .primary-button-small {
        margin-top: $padding;
        display: none;
        svg {
          margin-left: $small-padding;
        }
      }
    }
    &:hover {
      .primary-button-small {
        display: inline-block;
      }
    }
  }
  .homepage-header--button {
    padding: $padding $inner-block-margin;
    font-weight: bold;
    font-size: 13px;
    display: none;
    position: relative;
    left: 0;
    top: $padding;

    svg {
      margin-left: $basic-padding;
    }

    &:hover {
      display: inline-block;
    }
  }
</style>



