<template>
  <section class="sizecare" id="sizecare">
    <div class="slogan-sizecare">
      <SloganLine v-bind:content="sloganContent"></SloganLine>
    </div>
    <div class="details-card card-shadow">
      <vs-row>
        <vs-col vs-w="6" class="details-card--leftside">
          <h3>Brevis Tale Basic Men`s T- Shirt</h3>
          <img src="../assets/sizecare/tshirt.png" alt="">
          <h4>Measurement table (cm)</h4>
          <div class="details-card--table">
          </div>
        </vs-col>
        <vs-col vs-w="6" class="details-card--rightside">
          <h3>Product details</h3>
          <div class="materials-container">
            <p>Material: <b>100 % cotton</b></p>
            <p>Weight: <b>180 g/m2</b></p>
          </div>
            <vs-row vs-align="flex-start" class="list-container"
                    vs-type="flex" vs-justify="center" vs-w="12">
              <vs-col vs-w="6">
                <h4>Workmanship</h4>
                <ul class="list-workmanship">
                  <li class="list-workmanship--item">rib neckline with elastane</li>
                  <li class="list-workmanship--item">back neck tape</li>
                  <li class="list-workmanship--item">shoulder seams with stay</li>
                  <li class="list-workmanship--item">tape - double seams</li>
                  <li class="list-workmanship--item">tubular</li>
                </ul>
              </vs-col>
              <vs-col vs-w="6">
                <h4>Сare instructions</h4>
                <ul class="list-instructions">
                  <li class="list-instructions--item item1">Dry clean, tetrachloroethylene</li>
                  <li class="list-instructions--item item2">Tumbler drying</li>
                  <li class="list-instructions--item item3">Wash at or below 60 C</li>
                  <li class="list-instructions--item item4">Do not bleach </li>
                  <li class="list-instructions--item item5">Iron at medium temperature</li>
                </ul>
              </vs-col>
            </vs-row>
        </vs-col>
      </vs-row>
    </div>
    <Footer></Footer>
  </section>
</template>

<script>
import SloganLine from '@/components/SloganLine';

export default {
  name: 'SizeCare',
  components: {
    SloganLine,
  },
  data() {
    return {
      sloganContent: {
        header: 'SIZE & CARE',
        text: 'We understand how difficult it is to choose the wright size of clothes on the Internet. That is why we have prepared a size guide of BrevisTale shirts for you.\n',
      },
    };
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
.slogan-sizecare {
  background-color: white;
}
  .details {
    text-align: center;
    &-card{
      margin-top: $block-margin;
      background-color: white;
      border-radius: 8px;
      position: relative;
      display: inline-block;
      width: 80%;
      h3 {
        color: black;
        font-weight: 900;
        margin-bottom: $padding;
      }
      img {
        margin-bottom: $padding;
      }
      h4 {
        font-weight: 900;
        font-size: 13px;
        color: black;
        margin-bottom: $basic-padding;
      }
      p {
        font-size: 13px;
        color: black;
        b {
          font-weight: 900;
        }
      }
      ul, li {
        list-style: none;
        width: auto;
      }
      &--leftside{
        margin: $inner-block-margin 0;
        .details-card--table {
          height: 85px;
          width: 100%;
          background: url("../assets/sizecare/table.svg") no-repeat;
          position: relative;
          left: 10%;
          top: 0;
        }
      }
      &--rightside{
        margin: $inner-block-margin 0;
        .list{
          &-container {
            padding-left: $inner-block-margin;
            color: black;
            text-align: left;
            li{
              width: 100%;
              margin-bottom: $small-padding;
              position: relative;
            }
          }

          &-workmanship {
            li {
              &:before {
                content: '';
                position: absolute;
                height: 2px;
                width: 7px;
                background: url('../assets/sizecare/line_icon.svg');
                left: -15px;
                top: 10px;
              }
            }
          }
          &-instructions {
            &--item {
              &:before {
                content: '';
                position: absolute;
                height: 13px;
                width: 13px;
                left: -20px;
                top: 4px;
              }
              &.item1 {
                &:before {
                  background: url('../assets/sizecare/care_icons/1.svg');
                }
              }
              &.item2 {
                &:before {
                  background: url('../assets/sizecare/care_icons/2.svg');
                }
              }
              &.item3 {
                &:before {
                  background: url('../assets/sizecare/care_icons/3.svg');
                }
              }
              &.item4 {
                &:before {
                  background: url('../assets/sizecare/care_icons/4.svg');
                }
              }
              &.item5 {
                &:before {
                  background: url('../assets/sizecare/care_icons/5.svg');
                }
              }
            }
            &--item2 {
            }
            li {
            }
          }
        }
        .materials-container {
          margin-bottom: $padding;

        }
      }
    }
  }
</style>



