<template>
  <div class="shop">
    <div v-if="nextPageFlag === false" class="curtains-container">
      <CurtainShopCard
        @clicked="onClickCard"
        class="curtains-element"
      ></CurtainShopCard>
    </div>
    <div v-else class="shop-container">
      <vs-row
        vs-justify="center"
        vs-align="center"
        class="shop-container--label"
        vs-w="12"
        id="label">
        <vs-col vs-w="8">
          <p><b>Historia o Brevis</b> pozostała główną składową idei,
            więc postanowiliśmy rozpocząć naszą podróż dokładnie z jej.
            Dla nas jest to bardzo ważne i pod tym względem pozostanie
            wydano tylko <b>50 koszulek</b>. Nie zwlekaj!</p>
        </vs-col>
      </vs-row>
      <vs-row
        vs-justify="center"
        vs-align="flex-start"
        vs-w="12"
        class="shop-container--product"
      >
        <vs-col vs-w="5" class="shop-container--product-carousel">
          <div class="main-carousel">
            <vs-row vs-w="12">
              <vs-col vs-w="12">
                <carousel :per-page="1" :navigate-to="slideNumber" :paginationEnabled="false">
                  <slide
                    v-for="image in productImages"
                    v-bind:key="image.id">
                    <img :src="image.photo">
                  </slide>
                </carousel>
              </vs-col>
            </vs-row>
          </div>
          <div class="preview-carousel">
            <vs-row vs-w="12" vs-align="center">
              <vs-col vs-w="8">
                <carousel :per-page="5" :paginationEnabled="false">
                  <slide
                    v-for="image in productImages"
                    v-bind:key="image.id"
                    @slideclick="currentSlide(image.id)">
                    <img :src="image.preview">
                  </slide>
                </carousel>
              </vs-col>
              <vs-col vs-w="2" vs-align="flex-end" class="preview-carousel--share">
                <font-awesome-icon icon="share"/>
                <p>Share</p>
              </vs-col>
            </vs-row>
          </div>
        </vs-col>
        <vs-col vs-w="3" class="shop-container--product-description">
          <h4 class="product-title">{{product.title}}</h4>
          <h4 class="product-price">{{product.price}} PLN</h4>
          <p class="product-counter product-p">Left: <b>{{product.stock}}</b></p>
          <p class="product-color product-p">Colour: <b>{{product.color}}</b></p>
          <span class="product-color-icon"></span>
          <div class="divider"></div>
          <dropdown
            class="product-size"
            :options="sizeList"
            :selected="product.size"
            v-on:updateOption="methodToRunOnSelect"
            :placeholder="'Select an Item'">
          </dropdown>
          <div class="product-buy-button">
            <vs-button
              color="danger"
              class="primary-button"
              type="filled"
              @click="addProductToCart(product)"
            >ADD TO BAG</vs-button>
          </div>
          <div class="divider"></div>
          <div class="product-info">
            <a href="" id="delivery">Delivery and returns info</a>
            <p class="product-p">Still unsure what size to get?</p>
            <a href="" id="size">Find your recommended size.</a>
          </div>
        </vs-col>
      </vs-row>
      <vs-row
        vs-justify="center"
        vs-align="flex-start"
        vs-w="12"
        class="shop-container--description"
      >
        <vs-col vs-w="4" id="left-side">
          <h4>PRODUCT DETAILS</h4>
          <p>{{product.description}}</p>
        </vs-col>
        <vs-col vs-w="4">
          <h4>DETAILS</h4>
          <ul class="details-list">
            <li class="details-list--item">- single jersey,</li>
            <li class="details-list--item">- 100 % cotton,</li>
            <li class="details-list--item">- 180 g/m², </li>
            <li class="details-list--item">- XS–XL Size,</li>
            <li class="details-list--item">- Oeko Tex Standard 100 certified,</li>
            <li class="details-list--item">-
              responsibly produced according to promodoro standard.
            </li>
          </ul>
        </vs-col>
      </vs-row>
      <vs-row vs-justify="center"
              vs-align="center"
              vs-w="12"
              class="shop-container--legend">
        <vs-col vs-w="12">
          <div class="legend-card card-shadow">
            <h2>THE STORY</h2>
            <img src="../assets/shop/Sketch.png" alt="">
            <p>Wędrowiec natrafił na (małą) wioskę znajdującą się u podnóża góry.
              W miejscowej tawernie poznał mieszkańca tej wioski, mężczyznę w podeszłym
              wieku o imieniu Brevis. Był on miejscowym bajarzem i podczas pobytu w
              tawernie opowiedział wędrowcowi kilka ciekawych historii o jego kraju.
              Brevis był ubrany jak wszyscy, za wyjątkiem jego górnej odzieży. Z wyglądu
              ona była bardzo stara, a od spodu widniały wyhaftowane obrazy jelenia i
              wilka. Zobaczywszy zainteresowanie swoim ubiorem opowiedział historię ze
              swojego życia. W młodości, zbierając chrust, został zaatakowany przez wilka.
              W nierównej walce stracił on wszystkie siły i czekał na śmierć, aż tu znikąd
              pojawił się jeleń. Wystawiwszy poroże nastraszył wilka i zmusił go do rychłej
              ucieczki. Na znak pamięci tego dnia, Brevis pozostawił sobie zniszczoną odzież,
              w której był w tamtym dniu. Zaszywszy podarte miejsca obrazami dwóch zwierząt,
              zawsze jest chętny do opowiedzenia wszystkim zainteresowanym historię o
              niespodziewanym ratunku.
            </p>
          </div>
        </vs-col>
      </vs-row>
      <vs-row vs-w="12"
              vs-justify="center"
              vs-align="flex-start">
        <vs-col vs-w="12" class="shop-container--images">
          <div >
            <vs-col vs-w="6" class="block-two">
              <img src="https://picsum.photos/id/871/572/317?grayscale&blur=2" alt="">
            </vs-col>
            <vs-col vs-w="6" class="block-two">
              <img src="https://picsum.photos/id/851/572/317?grayscale&blur=2" alt="">
            </vs-col>
            <vs-col vs-w="4" class="block-three">
              <img src="https://picsum.photos/400/400?image=33" alt="">
            </vs-col>
            <vs-col vs-w="4" class="block-three">
              <img src="https://picsum.photos/400/400?image=34" alt="">
            </vs-col>
            <vs-col vs-w="4" class="block-three">
              <img src="https://picsum.photos/400/400?image=35" alt="">
            </vs-col>
          </div>
        </vs-col>
      </vs-row>
      <vs-button
        color="primary"
        class="shop-container--to-up card-shadow"
        type="filled"
        href="#label">GO UP
        <font-awesome-icon icon="arrow-up">
        </font-awesome-icon>
      </vs-button>
    </div>
    <Footer></Footer>
  </div>
</template>

<script>
import CurtainShopCard from '@/components/shop/CurtainShopCard';
import { Carousel, Slide } from 'vue-carousel';
import dropdown from 'vue-dropdowns';
import { mapState, mapActions } from 'vuex';

export default {
  name: 'Shop',
  components: {
    CurtainShopCard,
    Carousel,
    Slide,
    dropdown,
  },
  data() {
    return {
      nextPageFlag: false,
      slideNumber: 0,
      size: { name: 'XS' },
      sizeList: [{ name: 'XS' }, { name: 'S' }, { name: 'M' }],
      buttonData: [{
        id: 0,
      }, {
        id: 1,
      }, {
        id: 2,
      }],
      productImages: [{
        id: 0,
        photo: 'static/shop/product_main.png',
        preview: 'static/shop/small_preview.png',
      }, {
        id: 1,
        photo: 'static/shop/product_main.png',
        preview: 'static/shop/small_preview.png',
      }, {
        id: 2,
        photo: 'static/shop/product_main.png',
        preview: 'static/shop/small_preview.png',
      }, {
        id: 3,
        photo: 'static/shop/product_main.png',
        preview: 'static/shop/small_preview.png',
      }, {
        id: 4,
        photo: 'static/shop/product_main.png',
        preview: 'static/shop/small_preview.png',
      }],
    };
  },
  methods: {
    onClickCard(value) {
      this.nextPageFlag = value;
    },
    currentSlide(id) {
      this.slideNumber = id;
    },
    methodToRunOnSelect(payload) {
      this.product.size = payload;
    },
    ...mapActions('cart', [
      'addProductToCart',
    ]),
  },
  computed: {
    ...mapState({
      product: state => state.cart.product,
    }),
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .curtains {
    &-container {
      margin-top: $block-margin;
    }
  }
  .shop {
    &-container {
      width: 100%;
      h4 {
        font-size: 21px;
        font-weight: 900;
        color: black;
      }
      &--label {
        font-size: 24px;
        background-color: $primary-color;
        padding: $padding 0;
        p {
          color: white;
          font-weight: 400;
        }
        b {
          font-weight: 900;
        }
      }
      &--product {
        margin-top: $inner-block-margin;
        &-description {
          max-width: 347px;
          width: 100%;
          height: 100%;
          background-color: white;
          border-radius: 5px;
          padding: $inner-block-margin;
          text-align: left;
          .product-title {
            margin-bottom: $padding;
          }
          .product-price {
            margin-bottom: $basic-padding;
            padding-bottom: $basic-padding;
            border-bottom: 1px solid $bottom-border-color;
          }
          .product-p {
            font-size: 13px;
            font-weight: 400;
            color: black;

            b {
              font-weight: 900;
            }
          }
          .product-counter {
            padding-bottom: $basic-padding;
            border-bottom: 1px solid $bottom-border-color;
            margin-bottom: $basic-padding;
          }
          .product-color {
            padding-bottom: $basic-padding;
          }
          .product-color-icon {
            position: relative;
            display: inline-block;
            width: 21px;
            height: 21px;
            background-color: $primary-color;
            border-radius: 5px;
          }
          .divider {
            margin-top: $basic-padding;
            margin-bottom: $basic-padding;
          }
          .product-size {
            display: block;
            font-size: 13px;
            color: black;
            border: 1px solid black;
            border-radius: 5px;
            .dropdown-toggle {
              text-align: center;
              color: black;
              margin-bottom: 0;
              font-weight: 900;
              border: none;
              height: 100%;
              background: transparent;
              &:hover {
                background-color: transparent;
              }
            }
          }
          .product-buy-button {
            text-align: center;
            .primary-button{
              display: inline-block;
              margin-top: $padding;
              padding: $small-padding $inner-block-margin;
            }
          }
          .product-info {
            font-size: 13px;
            font-weight: 400;
            color: black;
            padding-left: $inner-block-margin;
            a {
              position: relative;
              display: inline-block;
              color: black;
              padding-bottom: $basic-padding;
            }
            #delivery {
              &:before {
                position: absolute;
                content: '';
                height: 16px;
                width: 16px;
                background: url("../assets/shop/delivery_icon.png") no-repeat;
                background-size: contain;
                top: 0;
                left: -$padding;
              }
            }
            #size {
              &:before {
                position: absolute;
                content: '';
                height: 16px;
                width: 16px;
                background: url("../assets/shop/size_icon.png") no-repeat;
                background-size: contain;
                top: -$basic-padding;
                left: -$padding;
              }
            }
          }
        }
        .main-carousel {
          max-width: 512px;
          max-height: 486px;
          width: 100%;
          height: 100%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .preview-carousel {
          img {
            margin-top: $padding;
            max-width: 53px;
            max-height: 53px;
            width: 100%;
            height: 100%;
          }
          &--share {
            padding-top: $basic-padding;
            padding-left: $padding;

            p {
              font-size: 13px;
              color: black;
              font-weight: 400;
            }
          }
          .VueCarousel-slide {
            max-width: 53px;
            margin-right: $padding;
          }
        }
      }
      &--description {
        margin-top: $inner-block-margin;
        background-color: white;
        padding: $padding 0;
        p {
          font-size: 13px;
          color: black;
          font-weight: 400;
          text-align: left;
        }
        h4 {
          margin-bottom: $padding;
        }
        ul {
          text-align: left;
          margin-left: $inner-block-margin;
          font-size: 13px;
          font-weight: 400;
        }
        #left-side {
          padding-right: $inner-block-margin;
          border-right: 1px solid $bottom-border-color;
        }
      }
      &--legend {
        margin-top: $inner-block-margin;
        width: 90%;
        h2 {
          color: $primary-color;
        }
        img {
          max-width: 840px;
          width: 100%;
          height: 100%;
          padding: $padding 0;
        }
        p {
          position: relative;
          padding: 0 $block-margin $inner-block-margin;
          font-size: 18px;
          color: $p-color;
          font-weight: 400;
          &:before {
            content: '';
            background-image: url("../assets/about/left-quote.svg");
            position: absolute;
            left: 50px;
            top: -35px;
            height: 46px;
            width: 46px;
          }
          &:after {
            content: '';
            background-image: url("../assets/about/right-quote.svg");
            position: absolute;
            right: 50px;
            bottom: 5px;
            height: 46px;
            width: 46px;
          }
        }
        .legend-card {
          margin: 0 $padding;
          padding: $padding 0;
          background-color: white;
        }
      }
      &--images {
        background-color: white;
        padding: $padding 0;
        margin-top: $inner-block-margin;
        height: 100%;
        img{
          width: 100%;
          height: 100%;
        }
        .block-two {
          img {
            max-width: 572px;
            max-height: 317px;
          }
          padding: $small-padding;
        }
        .block-three {
          img {
            max-width: 376px;
            max-height: 208px;
          }
          padding: $small-padding;
        }
      }
      &--to-up {
        margin-top: $padding;
        color: black;
        padding: $basic-padding 60px;
        svg {
          margin-left: $small-padding;
        }
        &:hover {
          background-color: white;
        }
      }
    }
  }
</style>



