<template>
  <div class="slogan-line">
    <vs-row>
      <vs-col v-if="text !== ''" vs-type="flex" vs-justify="center" vs-align="center"
              vs-w="10" vs-offset="1" class="slogan-card">
        <h2 class="slogan-card--header">{{header}}</h2>
        <p class="slogan-card--text">
          {{text}}</p>
      </vs-col>
      <vs-col v-else vs-type="flex" vs-justify="center" vs-align="center"
              vs-w="10" vs-offset="1" class="slogan-card" >
        <h2 class="slogan-card--header-single">{{header}}</h2>
      </vs-col>
    </vs-row>
  </div>
</template>

<script>
export default {
  name: 'SloganLine',
  props: ['content'],
  data() {
    return {
      header: this.content.header,
      text: this.content.text,
    };
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
.slogan {
  &-card {
    margin: $block-margin*0.75 0;
    &--header {
      margin-top: auto;
      margin-bottom: auto;
      display: inline-block;
      width: 26%;
      text-align: right;
      margin-right: 3%;
      color: $primary-color;
      &-single {
        width: 100%;
        color: $primary-color;
      }
    }
    &--text {
      text-align: left;
      width: 70%;
      display: inline-block;
      font-size: 25px;
      color: black;
    }
  }
}
</style>



