<template>
  <vs-row vs-w="12">
    <vs-col vs-w="4">
      <div class="shop-card" >
        <div class="shop-card-container">
          <img
            @mouseenter="isHover=true"
            @mouseleave="isHover=false"
            :src="[ isHover ? card.hoverUrl : card.basicUrl ]"
            :class="[ isHover ? 'hover' : 'not-hover' ]"
            alt=""
            class="shop-card-container--image"
          >
          <div v-if="isHover" class="shop-card-container--content"
               @mouseenter="isHover=true"
               @mouseleave="isHover=false">
            <h3>Help comes from unexpected places</h3>
            <vs-button
              color="primary"
              class="shop-card-container--button card-shadow"
              type="filled"
              @click="goToShop">LOOK CLOSER</vs-button>
          </div>
        </div>
      </div>
    </vs-col>
    <vs-col vs-w="4">
      <div class="shop-card">
        <div class="shop-card-container">
          <div class="shop-card-container--curtains">
            <img
              @mouseenter="isHoverCurtains1=true"
              @mouseleave="isHoverCurtains1=false"
              src="../../assets/posts/curtains.png"
              alt="">
            <div
              @mouseenter="isHoverCurtains1=true"
              @mouseleave="isHoverCurtains1=false"
              :class="[ isHoverCurtains1 ? 'hover' : 'not-hover' ]"></div>
          </div>
        </div>
      </div>
    </vs-col>
    <vs-col vs-w="4">
      <div class="shop-card">
        <div class="shop-card-container">
          <div class="shop-card-container--curtains">
            <img
              @mouseenter="isHoverCurtains2=true"
              @mouseleave="isHoverCurtains2=false"
              src="../../assets/posts/curtains.png"
              alt="">
            <div
              @mouseenter="isHoverCurtains2=true"
              @mouseleave="isHoverCurtains2=false"
              :class="[ isHoverCurtains2 ? 'hover' : 'not-hover' ]"></div>
          </div>
        </div>
      </div>
    </vs-col>
  </vs-row>
</template>

<script>
export default {
  name: 'CurtainShopCard',
  props: [],
  data() {
    return {
      card: {
        basicUrl: 'static/shop/card.png',
        hoverUrl: 'static/shop/CARDhover.png',
      },
      isHover: false,
      isHoverCurtains1: false,
      isHoverCurtains2: false,
    };
  },
  methods: {
    goToShop() {
      this.$emit('clicked', true);
    },
  },
  computed: {
    isOpen() {
      if (this.card.id === 0) {
        return true;
      }
      return false;
    },
  },
};
</script>

<style lang="scss">
@import "~@/styles/style.scss";
.shop-card{
  position: relative;
  &-container{
    transition: all 0.5s;
    &--image{
      width: 80%;
      height: 80%;
      transition: height 1s, width 2s;
      &.hover {
        width: 85%;
        height: 85%;
      }
    }
    &--curtains {
      width: 90%;
      height: 90%;
      &.hover {
        &:before{
          content:'';
          position: absolute;
          background: url("../../assets/shop/soon_lighter.png");
          height: 327px;
          width: 157px;
          top: 0;
          left: 30%;
        }
      }
      .hover {
        position: absolute;
        background: url("../../assets/shop/soon_lighter.png");
        height: 327px;
        width: 157px;
        top: 0;
        left: 30%;
        z-index: 2;
      }
    }
    &--content{
      position: absolute;
      text-align: center;
      width: 50%;
      top: 40%;
      left: 25%;
    }
    &--button{
      color: black;
      font-weight: 400;
      font-size: 13px;
      padding: $basic-padding $inner-block-margin;
      margin-top: $padding;
    }
  }
}
</style>



