import Vue from 'vue';
import Router from 'vue-router';
import Homepage from '@/pages/Homepage';
import SizeCare from '@/pages/SizeCare';
import Contact from '@/pages/Contact';
import AboutUs from '@/pages/AboutUs';
import Shop from '@/pages/Shop';
import Cart from '@/pages/Cart';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage,
    },
    {
      path: '/sizecare',
      name: 'SizeCare',
      component: SizeCare,
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact,
    },
    {
      path: '/aboutus',
      name: 'AboutUs',
      component: AboutUs,
    },
    {
      path: '/shop',
      name: 'Shop',
      component: Shop,
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart,
    },
  ],
});



