<template>
  <div class="homepage-section">
    <div v-if="post.alignSide==='right'" class="right-align">
      <vs-row vs-align="center">
        <vs-col s-type="flex" vs-w="6" class="homepage-section__left">
          <img :src="post.img_url" alt="" >
        </vs-col>
        <vs-col s-type="flex" vs-w="6" class="homepage-section__right">
          <p>{{post.text}}</p>
        </vs-col>
      </vs-row>
    </div>
    <div v-else class="left-align">
      <vs-row vs-align="center">
        <vs-col s-type="flex" vs-w="6" class="homepage-section__left">
          <p>{{post.text}}</p>
        </vs-col>
        <vs-col s-type="flex" vs-w="6" class="homepage-section__right">
          <img :src="post.img_url" alt="" >
        </vs-col>
      </vs-row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BasicSection',
  props: ['content'],
  data() {
    return {
      post: {
        id: this.content.id,
        img_url: this.content.img_url,
        text: this.content.text,
        alignSide: this.content.alignSide,
      },
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import "~@/styles/style.scss";
  .homepage-section {
    .right-align {
      background-color: $bg-color-grey;
      p {
        color: black;
      }
    }
    height: 540px;
    img {
      text-align: right;
    }

    &__left {
      width: 100%;
      text-align: right;
      padding-left: 156px;
      padding-right: 45px;
      margin: 110px 0px;
    }
    &__right {
      width: 100%;
      text-align: left;
      padding-left: 45px;
      padding-right: 156px;
      margin: 110px 0px;
    }
  }
</style>



