<template>
  <section class="footer" id="footer">
    <div class="footer-image">
      <vs-row>
        <vs-col vs-w="12">
          <img class="footer-image--element" src="../assets/footer.png" alt="">
        </vs-col>
      </vs-row>
    </div>
    <div class="footer-container">
      <vs-row>
        <vs-col s-type="flex" vs-align="center" vs-w="4">
          <ul class="footer-nav">
            <h3>Customer service</h3>
            <li class="footer-nav--item"><a href="">Privacy Policy</a></li>
          </ul>
        </vs-col>
        <vs-col s-type="flex" vs-align="center" vs-w="4">
          <ul class="footer-nav">
            <h3>Navigation</h3>
            <li class="footer-nav--item"><router-link to="/shop">SHOP</router-link></li>
            <li class="footer-nav--item"><router-link to="/aboutus">ABOUT US</router-link></li>
            <li class="footer-nav--item"><router-link to="/sizecare">SIZE & CARE</router-link></li>
            <li class="footer-nav--item"><router-link to="/aboutus">CONTACT</router-link></li>
          </ul>
        </vs-col>
        <vs-col s-type="flex" vs-align="center" vs-w="4">
          <ul class="footer-nav social-nav">
            <h3>Social media</h3>
            <li class="footer-nav--item"><font-awesome-icon
              :icon="['fab', 'facebook-square']" /></li>
            <li class="footer-nav--item"><font-awesome-icon
              :icon="['fab', 'instagram']" /></li>
            <li class="footer-nav--item"><font-awesome-icon
              :icon="['fab', 'facebook-messenger']" /></li>
          </ul>
        </vs-col>
      </vs-row>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Footer',

};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .footer {

    &-image {
      width: 100%;
      margin-top: 50px;
      &--element {
        position: relative;
        top: 5px;
        left: 0;
        }
      }
    &-container {
      background-color: $primary-color;
      padding: 0 110px;
      color: white;
      padding: 50px 0;
      h3 {
        padding-bottom: $basic-padding;
      }
      ul {
        padding-left: 33%;
      }
      ul, li{
        text-align: left;
        list-style: none;
      }
      ul, li, a {
        font-size: 13px;
        font-weight: bold;
        color: white;
      }
    }
    &-nav {
      &.social-nav {
        li {
          display: inline-block;
          padding-left: $basic-padding;
        }
        .svg-inline--fa {
          font-size: 21px;
          height: 21px;
        }
      }
    }
  }
</style>



