<template>
  <div class="card">
    <div class="card-container">
      <div class="card-curtain"
           :class="[ isOpen ? 'card-curtain--open' : 'card-curtain--closed' ]"
           @click="openCurtains()" @mouseenter="isHover=true" @mouseleave="isHover=false"></div>
      <div class="card-curtain--lighter"
           @click="openCurtains()"
           @mouseenter="isHover=true" @mouseleave="isHover=false"
           v-if="lightOn" :style="lighterStyle">
      </div>
      <div class="card-container--text card-shadow">
        <p>{{card.text}}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CurtainCard',
  props: ['content'],
  data() {
    return {
      card: {
        id: this.content.id,
        text: this.content.text,
        lighterUrl: this.content.lighterUrl,
      },
      isOpen: false,
      isHover: false,
      lighterStyle: {
        background: `url('${this.content.lighterUrl}') no-repeat`,
      },
    };
  },
  methods: {
    openCurtains() {
      this.isOpen = !this.isOpen;
      this.isHover = !this.isHover;
    },
  },
  computed: {
    lightOn() {
      if (this.isOpen === true) {
        return false;
      }
      return this.isHover;
    },
  },
};
</script>

<style lang="scss">
@import "~@/styles/style.scss";
.card {
  display: inline-block;
  &-curtain {
    position: absolute;
    height: 447px;
    width: 393px;
    z-index: 1;
    background: url("../../assets/posts/curtains.png") no-repeat;
    transition: all 0.5s;
    &--lighter {
      position: absolute;
      left: 30%;
      width: 157px;
      height: 327px;
      z-index: 1;
    }
    &--open {
      height: 110px;
      background: url("../../assets/posts/open_curtains.png") no-repeat;
    }
    &--closed {
      &:hover {
        &--lighter {
          display: block;
        }
      }
      height: 447px;
    }
  }
  &-container {
    position: relative;
    display: inline-flex;
    height: 448px;
    width: 393px;
    z-index: 0;
    margin: 0 $basic-padding*2;
    &--text {
      text-align: center;
      margin: 0 7% 6%;
      padding: 30% $padding 0px;
      background-color: white;
      p {
        border-top: 3px solid $primary-color;
        border-bottom: 3px solid $primary-color;
        padding: $padding 0;
        font-size: 18px;
        font-weight: 400;
      }
    }
  }
}
</style>



