<template>
  <div class="cart-delivery-container">
    <div class="cart-delivery">
      <h4 class="cart-delivery--title">Delivery method</h4>
      <div class="cart-delivery--type">
        <div class="delivery-truck"
             :class="[ selected === 'truck' ? 'delivery-truck-hover' : 'delivery-truck-no-hover' ]">
          <a v-on:click="selectDelivery('truck')">
            <img src="../../assets/cart/truck_nb.png" alt="">
          </a>
          <span>Paczkomaty</span>
        </div>
        <div class="delivery-courier delivery-courier-hover"
             :class="[ selected === 'courier' ? 'delivery-courier-hover' :
              'delivery-courier-no-hover' ]">
          <a v-on:click="selectDelivery('courier')">
            <img src="../../assets/cart/courier_nb.png" alt="">
          </a>
          <span>Kurier</span>
        </div>
      </div>
      <h4 class="cart-delivery--title">Shipping address</h4>
      <div class="cart-delivery--address">
        <vs-button
          color="rgb(255,255,255)"
          class="second-button second-button-shadow delivery-button-checkout"
          type="filled"
          v-if="!getDeliveryAddress"
          @click="activateForm"
        >ADD ADDRESS</vs-button>
        <div v-else>Name: {{getDeliveryAddress.name}},
          Surname: {{getDeliveryAddress.surname}},
          Email: {{getDeliveryAddress.email}},
          City: {{getDeliveryAddress.city}},
          Post Code: {{getDeliveryAddress.name}},
          Address: {{getDeliveryAddress.address}}
        </div>
      </div>
    </div>
    <div class="delivery-form" v-if="formActive">
      <h4>Address</h4>
      <div v-if="errors.length">
        <b>Please check form:</b>
        <ul>
          <li :key="index" v-for="(error, index) in errors">{{ error }}</li>
        </ul>
      </div>
      <vs-row vs-w="12">
        <vs-col vs-w="12">
          <label for="name">Imię</label>
          <input type="text" id="name" class="delivery-form-input--element" v-model="name">
        </vs-col>
      </vs-row>
      <vs-row vs-w="12">
        <vs-col vs-w="12">
          <label for="surname">Nazwisko</label>
          <input type="text" id="surname" class="delivery-form-input--element" v-model="surname">
        </vs-col>
      </vs-row>
      <vs-row vs-w="12">
        <vs-col vs-w="12">
          <label for="email">Email</label>
          <input type="email" id="email" class="delivery-form-input--element" v-model="email">
        </vs-col>
      </vs-row>
      <vs-row vs-w="12">
        <vs-col vs-w="12">
          <label for="email">Post-code</label>
          <input type="text" id="post-code" class="delivery-form-input--element" v-model="postCode">
        </vs-col>
      </vs-row>
      <vs-row vs-w="12">
        <vs-col vs-w="12">
          <label for="email">City</label>
          <input type="text" id="city" class="delivery-form-input--element" v-model="city">
        </vs-col>
      </vs-row>
      <vs-row vs-w="12">
        <vs-col vs-w="12">
          <label for="message">Address</label>
          <textarea rows=3 id="message" class="delivery-form-input--element multirow"
                    v-model="address"></textarea>
        </vs-col>
      </vs-row>
      <vs-row vs-w="12">
        <vs-col vs-w="12">
          <div class="delivery-form-button">
            <vs-button
              color="danger"
              class="primary-button-small second-button-shadow"
              type="filled"
              @click="saveAddress"
            >SAVE</vs-button>
          </div>
        </vs-col>
      </vs-row>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'DeliveryDetails',
  data() {
    return {
      selected: '',
      formActive: false,
      name: null,
      surname: null,
      email: null,
      postCode: null,
      city: null,
      address: null,
      errors: [],
    };
  },
  methods: {
    selectDelivery(select) {
      this.addDeliveryMethod({ name: select });
      this.selected = this.getDeliveryMethod;
    },
    activateForm() {
      this.formActive = !this.formActive;
    },
    validateForm() {
      if (this.name && this.surname && this.email
        && this.postCode && this.address && this.city) {
        return true;
      }
      this.errors = [];
      if (!this.name) {
        this.errors.push('Please enter your Name.');
      }
      if (!this.surname) {
        this.errors.push('Please enter your Surname.');
      }
      if (!this.email) {
        this.errors.push('Please enter your email.');
      }
      if (!this.postCode) {
        this.errors.push('Please enter your zip code.');
      }
      if (!this.city) {
        this.errors.push('Please enter your city.');
      }
      if (!this.address) {
        this.errors.push('Please enter your address.');
      }
      return false;
    },
    saveAddress() {
      if (this.validateForm) {
        const data = {
          name: this.name,
          surname: this.surname,
          email: this.email,
          postCode: this.postCode,
          city: this.city,
          address: this.address,
        };
        this.addDeliveryAddress(data);
        this.activateForm();
      } else {
        this.formActive = true;
      }
    },
    ...mapActions({
      addDeliveryAddress: 'cart/addDeliveryAddress',
      addDeliveryMethod: 'cart/addDeliveryMethod',
    }),
  },
  computed: {
    ...mapGetters({
      getDeliveryAddress: 'cart/getDeliveryAddress',
      getDeliveryMethod: 'cart/getDeliveryMethod',
    }),
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .cart-delivery {
    position: relative;
    background-color: white;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    padding: $inner-block-margin;
    margin-top: $inner-block-margin;
    h4 {
      margin-bottom: $basic-padding;
    }
    &--type {
      width: 100%;
      margin-bottom: $padding;
      a {
        display: inline-block;
        width: 100%;
      }
      img {
        width: 100%;
        max-width: 37px;
        border: 1px solid black;
        border-radius: 5px;
      }
      span {
      }
      .delivery-truck {
        display: inline-block;
        text-align: center;
        width: 49%;
        &-hover {
          img {
            border: 1px solid $primary-color;
            @include button-shadow();
          }
        }
        &-no-hover {
          img {
            border: 1px solid black;
          }
        }
      }
      .delivery-courier {
        display: inline-block;
        text-align: center;
        width: 49%;
        &-hover {
          img {
            border: 1px solid $primary-color;
            @include button-shadow();
          }
        }
        &-no-hover {
          img {
            border: 1px solid black;
          }
        }
      }
    }
    .delivery-button-checkout {
      margin-top: $basic-padding;
    }
  }
  .delivery-form {
    position: relative;
    width: 100%;
    top: $inner-block-margin;
    background-color: white;
    border-radius: 5px;
    padding: $inner-block-margin $padding;
    text-align: left;
    & h4 {
      margin-bottom: $padding;
    }
    & label {
      display: block;
      font-weight: 400;
      font-size: 13px;
      color: black;
      margin-bottom: $small-padding;
    }
    &-input--element {
      display: block;
      width: 100%;
      height: 38px;
      border: 1px solid black;
      border-radius: 5px;
      margin-bottom: $padding;
      &.multirow {
        height: 114px;
      }
    }
    .delivery-form-button {
      display: inline-block;
      text-align: center;
      width: 100%;
    }
    .primary-button-small {
      width: 80%;
      margin: auto;
    }
  }
</style>



